.container {
  width: calc(100vw - 60px);
  height: 100svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10;

  h1 {
    font-size: clamp(33px, 5vw, 80px);
    background: url(../../assets/space.jpg);
    background-clip: text;
    color: transparent;
    animation: textBackgroundAnim 30s linear infinite;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
    pointer-events: none;
    user-select: none;
    height: 150px;
  }

  @media (max-width: 768px) {
    h1 {
      height: 70px;
    }
  }
}

@keyframes textBackgroundAnim {
  to {
    background-position: -200%;
  }
}
